* {
    font-size: 18px;
}

body {
    display: flex;
    justify-content: center;
    align-items: center;
}

.container {
    top: 50%;
    left: 50%;
    position: absolute;
    transform: translate(-50%, -50%);
    width: 256px;
    margin: 0 auto;
    text-align: center;
    border: 10px solid black;
    background-color: black;
    border-radius: 10px;
}

.keypad {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: minmax(60px, auto);
}

input[type="text"] {
    height: 75px;
    width: 250px;
    background-color: black;
    color: white;
    border: 0px;
    text-align: right;
}

button {
    margin: 5px;
    border: none;
    border-radius: 5px;
    background-color: #262834;
    color: white;
    cursor: pointer;
}

#clear {
    grid-column: 1/3;
    color: #262834;
}

#clearOne {
    color: #262834;
}

#equal {
    grid-column: 3/5;
    color: #262834;
}

input:focus,
input:active,
button:focus,
button:active{
    outline: none;
}

.highlight {
    background: #56cbdb;
}